


































import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import VueRouter from 'vue-router'
import page from '@/components/common-components/page.vue'
import pageView from '@/components/common-ui/page-view.vue'
import { deepClone } from '@/until/index'
@Component({
  components: {
    page,
    pageView
  }
})

export default class noticeConfig extends Vue {
  public $router: VueRouter | undefined
  public $route: any
  
  private className: string = '点位选择'

  private showBtn = false;

  private form: any = {
    licensePlate: ''
  }

  addTime () {
    this.form.timeValue.push('')
  }

  private dateTimePicker: any = null;

  private showDatePicker (index) {
    if (this.form.taskType === 'time') {
      this.dateTimePicker = this.$createDatePicker({
        title: '发车时间',
        min: new Date(),
        max: new Date(2060, 9, 20, 20, 59, 59),
        value: new Date(this.form.timeValue[index]),
        columnCount: 5,
        format: {
          year: 'YYYY年',
          month: 'MM月',
          date: 'D 日',
          hour: 'hh点',
          minute: 'mm分'
        },
        onSelect: (value)=>{
          this.selectHandle(index, value)
        },
        onCancel: this.cancelHandle
      })
      this.dateTimePicker.show()
    }
  }

  private formatDate(date) {
    const year = date.getFullYear();
    const month = ("0" + (date.getMonth() + 1)).slice(-2);
    const day = ("0" + date.getDate()).slice(-2);
    const hour = ("0" + date.getHours()).slice(-2);
    const minute = ("0" + date.getMinutes()).slice(-2);
    const second = ("0" + date.getSeconds()).slice(-2);
    return `${year}-${month}-${day} ${hour}:${minute}:${second}`;
  }

  private deleteTimeValue (index) {
    this.form.timeValue.splice(index, 1);
  }

  private selectHandle (index, value){
    this.$set(this.form.timeValue, index, this.formatDate(value))
  }
  private cancelHandle (){}

  private fields = [
    {
      type: 'input',
      modelKey: 'licensePlate',
      label: '车牌号',
      props: {
        placeholder: '请输入'
      },
      rules: {
        required: true
      }
    },
    {
      type: 'input',
      modelKey: 'number',
      label: '座位数',
      props: {
        placeholder: '请输入',
        type: 'number'
      },
      rules: {
        required: true
      }
    }
  ]




  private hidePopup (refId) {
    const component: any = this.$refs[refId]
    component.hide()
  }

  private submitHandler (e) {
    e.preventDefault()
    console.log(this.form)
    this.$store.commit('SET_BACK_DATA', {
      route: 'fleetTaskCarAdd',
      data: {
        backInfo: this.backInfo,
        data: this.form
      }
    })
    this.$router.back();
  }

  backInfo = {}
  backType = ''

  mounted () {
    let that = this
    let query = this.$route.query
    let type = query.type;
    this.backInfo = query.backInfo;
    if (this.backInfo.type === 'update') {
      this.form = this.backInfo.info;
    }
    this.className = '车辆信息'
  }

  showToastType () {
    const toast = (this as any).$createToast({
      txt: '保存成功',
      type: 'correct'
    })
    toast.show()
  }

}
